.App{
  text-align: center;
}

.progress-text {
  text-align: center;
  margin-top: 40px;
}

.question-text {
  text-align: center;
  margin-top: 30px;
  display: block;
}

ul.answers-wrapper {
  width: 70%;
  margin: 0 auto;
  margin-top: 20px;
}

ul.answers-wrapper li{
  list-style: none;
  height: 30px;
 
  margin-bottom: 5px;
  background: #eceff1;
    border: 1px solid #d3d9df;
    padding: 5px 5px 8px;
    display: block;
    color: #3c4f59;
    position: relative;
    font-size: 18px;
    cursor: pointer;
}

ul.answers-wrapper li:hover{
  background-color: bisque;
}